define('frontend/routes/studies/index', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		model: function () {
			return this.store.find("study");
		},
		actions: {
			"delete": function (study) {
				study.destroyRecord();
				this.transitionTo("studies");
			}
		}
	});

});