define('frontend/routes/policies/new', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		model: function () {
			return this.store.createRecord("pyp");
		},
		deactivate: function () {
			var model = this.modelFor("policies/new");
			if (model.get("isNew")) {
				model.destroyRecord();
			}
		} });

});