define('frontend/routes/policies/index', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		model: function () {
			return this.store.find("pyp");
		},
		actions: {
			"delete": function (pyp) {
				pyp.destroyRecord();
				this.transitionTo("policies");
			}
		}
	});

});