define('frontend/routes/policy/edit', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Route.extend({
		deactivate: function () {
			var model = this.modelFor("policy/edit");

			if (model.get("isDirty")) {
				model.rollback();
			}
		}
	});

});